<template>
  <div id>
    <v-container>
      <v-card style="margin-bottom:1%;">
        <v-card-title style="font-size:2rem">Histórico de Usuários</v-card-title>
        <v-card-title>
          Total de Usuários: &nbsp;
          <b>{{ this.totalUsers }}</b>
        </v-card-title>
        <v-btn @click="totalUsersPerMonth()" style="margin-top:1%; margin-bottom:1%;">Disponibilizar todos meses</v-btn>
        <v-btn @click="totalUsersPerLastFiveMonths()" style="margin-top:1%; margin-bottom:1%;"
          >Disponibilizar 5 últimos meses</v-btn
        >
        <v-btn @click="totalUsersPerLastTenMonths()" style="margin-top:1%; margin-bottom:1%;"
          >Disponibilizar 10 últimos meses</v-btn
        >
        <GChart type="ColumnChart" :data="chartUsersData" :options="chartOptions" />
      </v-card>
    </v-container>
    <v-container>
      <v-card style="margin-bottom:1%;">
        <v-card-title style="font-size:2rem">Histórico de Tasks</v-card-title>
        <v-card-title>
          Total de Tasks: &nbsp;
          <b>{{ this.tasksTotal }}</b>
        </v-card-title>
        <v-btn @click="totalTasksPerMonth()" style="margin-top:1%; margin-bottom:1%;">Disponibilizar todos meses</v-btn>
        <v-btn @click="totalTasksPerLastThreeMonths()" style="margin-top:1%; margin-bottom:1%;"
          >Disponibilizar 3 últimos meses</v-btn
        >
        <v-btn @click="totalTasksPerLastTenMonths()" style="margin-top:1%; margin-bottom:1%;"
          >Disponibilizar 10 últimos meses</v-btn
        >
        <GChart type="ColumnChart" :data="chartTasksData" :options="chartOptions" />
      </v-card>
    </v-container>
    <v-container>
      <v-card style="margin-bottom:1%;">
        <v-card-title style="font-size:2rem">Histórico de Mapas</v-card-title>
        <v-card-title>
          Total de Mapas: &nbsp;
          <b>{{ this.plansTotal }}</b>
        </v-card-title>
        <v-btn @click="totalPlansPerMonth()" style="margin-top:1%; margin-bottom:1%;">Disponibilizar todos meses</v-btn>
        <v-btn @click="totalPlansPerLastThreeMonths()" style="margin-top:1%; margin-bottom:1%;"
          >Disponibilizar 3 últimos meses</v-btn
        >
        <v-btn @click="totalPlansPerLastTenMonths()" style="margin-top:1%; margin-bottom:1%;"
          >Disponibilizar 10 últimos meses</v-btn
        >
        <GChart type="ColumnChart" :data="chartPlansData" :options="chartOptions" />
      </v-card>
    </v-container>
  </div>
</template>

<script>
import API from "@/api"
import { GChart } from "vue-google-charts"

export default {
  props: {
    chartdata: {
      type: Object,
      default: null
    },
    options: {
      type: Object,
      default: null
    }
  },

  components: {
    GChart
  },

  data() {
    return {
      // Array will be automatically processed with visualization.arrayToDataTable function

      chartOptions: {
        backgroundColor: "#ddd",
        "backgroundColor.stroke": "#123456789",
        "backgroundColor.strokeWidth": 10,
        legend: { position: "top", maxLines: 3 },
        chart: {
          title: "Quantidade de usuários por mês"
        }
      },
      loaded: false,
      usersTotal: 0,
      usersArray: [0, 0],
      usersDataArray: [["Mês/Ano", "Usuários"]],

      tasksTotal: 0,
      tasksArray: [0, 0],
      tasksDataArray: [["Mês/Ano", "Tasks"]],

      plansTotal: 0,
      plansArray: [0, 0],
      plansDataArray: [["Mês/Ano", "Mapas"]]
    }
  },

  computed: {
    activity() {
      return API.getActivity()
    },
    posts() {
      return API.getPost(3)
    },
    siteTrafficData() {
      return API.getMonthVisit
    },
    locationData() {
      return API.getLocation
    },
    chartUsersData() {
      return this.usersDataArray
    },
    chartTasksData() {
      return this.tasksDataArray
    },
    chartPlansData() {
      return this.plansDataArray
    }
  },
  methods: {
    totalUsersPerMonth() {
      this.$store
        .dispatch("countUsers")
        .then(async response => {
          //Restarting dataArray
          this.usersDataArray = [["Mês/Ano", "Usuários"]]
          this.totalUsers = response.data.totalUsers
          this.usersArray = await response.data.users.map(user => ({
            count: user.count,
            month: user._id.month,
            year: user._id.year
          }))

          let usersDisplayCount = 0
          let totalMonthsObjects = response.data.users.length - 1 //Removing last element(totalUsers)
          this.usersArray.map(user => {
            if (usersDisplayCount <= totalMonthsObjects) {
              user = this.usersArray[totalMonthsObjects - usersDisplayCount]
              if (user.month < 10) {
                this.usersDataArray.push([`0${user.month}/${user.year}`, user.count])
              } else {
                this.usersDataArray.push([`${user.month}/${user.year}`, user.count])
              }
            }
            usersDisplayCount++
          })
        })
        .catch(err => {
          console.log(err)
        })
    },

    totalUsersPerLastFiveMonths() {
      this.$store
        .dispatch("countUsers")
        .then(async response => {
          //Restarting dataArray
          this.usersDataArray = [["Mês/Ano", "Usuários"]]
          this.totalUsers = response.data.totalUsers

          this.usersArray = await response.data.users.map(user => ({
            count: user.count,
            month: user._id.month,
            year: user._id.year
          }))

          let usersDisplayCount = 0
          let totalMonthsObjects = response.data.users.length - 1 //Removing last element(totalUsers)
          this.usersArray.map(user => {
            if (usersDisplayCount < 5) {
              user = this.usersArray[totalMonthsObjects - usersDisplayCount]
              if (user.month < 10) {
                this.usersDataArray.push([`0${user.month}/${user.year}`, user.count])
              } else {
                this.usersDataArray.push([`${user.month}/${user.year}`, user.count])
              }
            }
            usersDisplayCount++
          })
        })
        .catch(err => {
          console.log(err)
        })
    },
    totalUsersPerLastTenMonths() {
      this.$store
        .dispatch("countUsers")
        .then(async response => {
          //Restarting dataArray
          this.usersDataArray = [["Mês/Ano", "Usuários"]]
          this.totalUsers = response.data.totalUsers
          this.usersArray = await response.data.users.map(user => ({
            count: user.count,
            month: user._id.month,
            year: user._id.year
          }))

          let usersDisplayCount = 0
          let totalMonthsObjects = response.data.users.length - 1 //Removing last element(totalUsers)
          this.usersArray.map(user => {
            if (usersDisplayCount < 10) {
              user = this.usersArray[totalMonthsObjects - usersDisplayCount]
              if (user.month < 10) {
                this.usersDataArray.push([`0${user.month}/${user.year}`, user.count])
              } else {
                this.usersDataArray.push([`${user.month}/${user.year}`, user.count])
              }
            }
            usersDisplayCount++
          })
        })
        .catch(err => {
          console.log(err)
        })
    },
    totalTasksPerMonth() {
      this.$store
        .dispatch("countTasks")
        .then(async response => {
          //Restarting dataArray
          this.tasksDataArray = [["Mês/Ano", "Tasks"]]
          this.tasksTotal = response.data.totalTasks
          this.tasksArray = await response.data.tasks.map(task => ({
            count: task.count,
            month: task._id.month,
            year: task._id.year
          }))

          let tasksDisplayCount = 0
          let totalMonthsObjects = response.data.tasks.length - 1 //Removing totalTasks - Last Index Array
          this.tasksArray.map(task => {
            if (tasksDisplayCount <= totalMonthsObjects) {
              task = this.tasksArray[totalMonthsObjects - tasksDisplayCount]
              if (task.month < 10) {
                this.tasksDataArray.push([`0${task.month}/${task.year}`, task.count])
              } else {
                this.tasksDataArray.push([`${task.month}/${task.year}`, task.count])
              }
            }
            tasksDisplayCount++
          })
        })
        .catch(err => {
          console.log(err)
        })
    },
    totalTasksPerLastThreeMonths() {
      this.$store
        .dispatch("countTasks")
        .then(async response => {
          //Restarting dataArray
          this.tasksDataArray = [["Mês/Ano", "Tasks"]]
          this.tasksTotal = response.data.totalTasks
          this.tasksArray = await response.data.tasks.map(task => ({
            count: task.count,
            month: task._id.month,
            year: task._id.year
          }))

          let tasksDisplayCount = 0
          let totalMonthsObjects = response.data.tasks.length - 1 //Removing totalTasks - Last Index Array
          this.tasksArray.map(task => {
            if (tasksDisplayCount < 3) {
              task = this.tasksArray[totalMonthsObjects - tasksDisplayCount]
              if (task.month < 10) {
                this.tasksDataArray.push([`0${task.month}/${task.year}`, task.count])
              } else {
                this.tasksDataArray.push([`${task.month}/${task.year}`, task.count])
              }
            }
            tasksDisplayCount++
          })
        })
        .catch(err => {
          console.log(err)
        })
    },
    totalTasksPerLastTenMonths() {
      this.$store
        .dispatch("countTasks")
        .then(async response => {
          //Restarting dataArray
          this.tasksDataArray = [["Mês/Ano", "Tasks"]]
          this.tasksTotal = response.data.totalTasks
          this.tasksArray = await response.data.tasks.map(task => ({
            count: task.count,
            month: task._id.month,
            year: task._id.year
          }))

          let tasksDisplayCount = 0
          let totalMonthsObjects = response.data.tasks.length - 1
          this.tasksArray.map(task => {
            if (tasksDisplayCount < 10) {
              task = this.tasksArray[totalMonthsObjects - tasksDisplayCount]
              if (task.month < 10) {
                this.tasksDataArray.push([`0${task.month}/${task.year}`, task.count])
              } else {
                this.tasksDataArray.push([`${task.month}/${task.year}`, task.count])
              }
            }
            tasksDisplayCount++
          })
        })
        .catch(err => {
          console.log(err)
        })
    },
    totalPlansPerMonth() {
      this.$store
        .dispatch("countPlans")
        .then(async response => {
          //Restarting dataArray
          console.log(response.data)
          this.plansDataArray = [["Mês/Ano", "Mapas"]]
          this.plansTotal = response.data.totalPlans
          this.plansArray = await response.data.plans.map(plan => ({
            count: plan.count,
            month: plan._id.month,
            year: plan._id.year
          }))

          let plansDisplayCount = 0
          let totalMonthsObjects = response.data.plans.length - 1
          this.plansArray.map(plan => {
            if (plansDisplayCount <= totalMonthsObjects) {
              plan = this.plansArray[totalMonthsObjects - plansDisplayCount]
              console.log(plan)
              if (plan.month < 10) {
                this.plansDataArray.push([`0${plan.month}/${plan.year}`, plan.count])
              } else {
                this.plansDataArray.push([`${plan.month}/${plan.year}`, plan.count])
              }
            }
            plansDisplayCount++
          })
        })
        .catch(err => {
          console.log(err)
        })
    },
    totalPlansPerLastTenMonths() {
      this.$store
        .dispatch("countPlans")
        .then(async response => {
          //Restarting dataArray
          this.plansDataArray = [["Mês/Ano", "Mapas"]]
          this.plansTotal = response.data.totalPlans
          this.plansArray = await response.data.plans.map(plan => ({
            count: plan.count,
            month: plan._id.month,
            year: plan._id.year
          }))

          let plansDisplayCount = 0
          let totalMonthsObjects = response.data.plans.length - 1
          this.plansArray.map(plan => {
            if (plansDisplayCount < 10) {
              plan = this.plansArray[totalMonthsObjects - plansDisplayCount]
              if (plan.month < 10) {
                this.plansDataArray.push([`0${plan.month}/${plan.year}`, plan.count])
              } else {
                this.plansDataArray.push([`${plan.month}/${plan.year}`, plan.count])
              }
            }
            plansDisplayCount++
          })
        })
        .catch(err => {
          console.log(err)
        })
    },
    totalPlansPerLastThreeMonths() {
      this.$store
        .dispatch("countPlans")
        .then(async response => {
          //Restarting dataArray
          this.plansDataArray = [["Mês/Ano", "Mapas"]]
          this.plansTotal = response.data.totalPlans
          this.plansArray = await response.data.plans.map(plan => ({
            count: plan.count,
            month: plan._id.month,
            year: plan._id.year
          }))

          let plansDisplayCount = 0
          let totalMonthsObjects = response.data.plans.length - 1 //Removing totalTasks - Last Index Array
          this.plansArray.map(plan => {
            if (plansDisplayCount < 3) {
              plan = this.plansArray[totalMonthsObjects - plansDisplayCount]
              if (plan.month < 10) {
                this.plansDataArray.push([`0${plan.month}/${plan.year}`, plan.count])
              } else {
                this.plansDataArray.push([`${plan.month}/${plan.year}`, plan.count])
              }
            }
            plansDisplayCount++
          })
        })
        .catch(err => {
          console.log(err)
        })
    }
  },

  async mounted() {
    try {
      this.totalUsersPerLastFiveMonths()
      this.totalTasksPerMonth()
      this.totalPlansPerMonth()
    } catch (e) {
      console.error(e)
    }
  }
}
</script>
